// 引入api接口
import {login} from "@/common/apis.js";

export default {
	data() {
		return {
			codeText: "获取验证码",
			btnState: true,
			phone: "",
			pwd: "",
			phoneState: false,
			pwdState: false,
			rememberPwdHide: true,
			saveObj: '',
		}
	},
	methods: {
		// 获取输入手机号
		inputPhone(e) {
			if (e.detail.value.length === 11) {
				this.phoneState = true;
				this.phone = e.detail.value;
			} else {
				this.phoneState = false;
			}
			this.loginState()
		},
		// 获取登录密码
		inputPwd(e) {
			if (e.detail.value.length === 6) {
				this.pwdState = true;
				this.pwd = e.detail.value;
			} else {
				this.pwdState = false;
			}
			this.loginState()
		},
		// 判断登录按钮状态
		loginState() {
			if (this.phoneState && this.pwdState) {
				this.btnState = false;
			} else {
				this.btnState = true;
			}
		},

		// 前往首页
		goIndex() {
			if (this.phone == 11111111111 && this.pwd == 888888) {
				this.saveObj = {
					phone: this.phone,
					pwd: this.pwd
				}
				// 登录请求
				login(this.saveObj).then(res=>{
					console.log(res);
				}).catch(err=>{
					console.log('请核对账号密码')
				})
				// uni.getStorage({
				// 	key: 'saveStata',
				// 	success: (res) => {
				// 		if (res.data) {
				// 			uni.reLaunch({
				// 				url: "../index/index"
				// 			})
				// 		} else {
				// 			// 提示保存密码
				// 			this.rememberPwdHide = false;
				// 		}
				// 	},
				// 	fail: () => {
				// 		// 提示保存密码
				// 		this.rememberPwdHide = false;
				// 	}
				// })
			} else {
				uni.showToast({
					title: '登陆失败，请核对后再登录！',
					icon: "none"
				});
			}
		},

		// 忘记密码
		forgetPwd() {
			uni.navigateTo({
				url: "../forgetPwd/forgetPwd"
			})
		},
		// 立即注册
		register() {
			uni.navigateTo({
				url: '../register/register'
			})
		},
		// 保存密码
		savePwd() {
			this.rememberPwdHide = true;
			// 保存状态到本地
			uni.setStorage({
				key: 'saveStata',
				data: true
			})
			// 账号密码保存
			uni.setStorage({
				key: 'name',
				data: JSON.stringify(this.saveObj),
				success() {
					uni.reLaunch({
						url: "../index/index"
					})
				},
				fail() {
					uni.showToast({
						title: '保存失败',
						icon: 'none'

					})
				}
			})
		},
		// 取消保存
		cancelSave() {
			// 保存状态到本地
			uni.setStorage({
				key: 'saveStata',
				data: false
			})
			// 移出本地数据
			uni.removeStorage({
				key: 'name',
				success: () => {
					this.rememberPwdHide = true;
					uni.reLaunch({
						url: "../index/index"
					})
				}
			})
		},
		// 微信登录




































































	},
	onLoad() {
		// 获取本地存储登录信息
		uni.getStorage({
			key: 'name',
			success: (data) => {
				var data = JSON.parse(data.data);
				this.phone = data.phone;
				this.pwd = data.pwd;
				this.phoneState = true;
				this.pwdState = true;
				this.loginState();
			}
		})
	},
	mounted() {







	}
}
